body {
  overflow-x: hidden;
}

.main-image {
  background: url(https://images7.alphacoders.com/443/443952.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 80vh;
  position: relative;
  background-attachment: fixed;
}

@media  (max-width: 1000px){
  .main-image {
    height: 60vh;
  }
}

@media  (max-width: 500px){
  .main-image {
    height: 58vh;
  }
}


.filter:after {
  background-color: rgba(0, 0, 0, 0.4);
  content: '';
  display: block;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
}


.card-title {
  overflow: hidden;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  font-size: 16px;
  white-space: nowrap;
  text-overflow: ellipsis;

}

html {

  margin: -5px;
}